
import React from 'react';
import { oc as option } from "ts-optchain";
import clsx from 'clsx';
/// MUI
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import Container from '@material-ui/core/Container';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import NotificationsIcon from '@material-ui/icons/Notifications';
import MenuItem from '@material-ui/core/MenuItem';
import QAIcon from "@material-ui/icons/QuestionAnswer";
import Button from '@material-ui/core/Button';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';

import { frenzyListItems, secondaryListItems } from 'layout/routeItems';
// Styles
import { withStyles, WithStyles, createStyles, Theme } from "@material-ui/core/styles";
// import Deposits from './Deposits';
// import Orders from './Orders';
/// Link
import Link from "next/link";
import ListItem from '@material-ui/core/ListItem';
import { useDispatch, useSelector } from "react-redux";
import Login from "./Login";
import { GrandReduxState } from "reduxStore/grand-reducer";
import { logout } from "utils/requests";
import { UserPrivate, Role } from 'typings';




const Dashboard: React.FC<ReactProps> = (props) => {

  const { classes } = props;
  const [open, setOpen] = React.useState(true);

  const loggedIn = useSelector<GrandReduxState, boolean>(state => {
    return state.reduxLogin.loggedIn
  })

  const user = useSelector<GrandReduxState, UserPrivate>(
    state => state.reduxLogin.user
  );
  const disabled = option(user).userRole() !== Role.PLATFORM_ADMIN;


  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);

  return (
    <div className={classes.root}>
      <AppBar position="absolute"
        className={clsx(
          classes.appBar,
          open && classes.appBarShift
        )}
      >
        <Toolbar className={classes.toolbar}>

          <IconButton
            edge="start"
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            className={clsx(classes.menuButton, open && classes.menuButtonHidden)}
          >
            <MenuIcon />
          </IconButton>

          <Link href="/">
            <ListItem button className={classes.adminHome}>
              <ListItemText primary={"Admin Home"}/>
            </ListItem>
          </Link>

          {
            disabled &&
            <ListItem button className={classes.notPlatformAdminMessage}>
              <ListItemText primary={"Status: Not a Platform Admin"}/>
            </ListItem>
          }

          <div className={classes.flexGrower}></div>

          {
            loggedIn
            ? <Button
                type="submit"
                variant="outlined"
                color="secondary"
                onClick={() => logout()}
              >
                Logout
              </Button>
            : <Login title={"Login"}/>
          }


        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        classes={{
          paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
        }}
        open={open}
      >
        <div className={classes.toolbarIcon}>
          <IconButton onClick={handleDrawerClose}>
            <ChevronLeftIcon />
          </IconButton>
        </div>
        <Divider />
        <List>{frenzyListItems}</List>
        <Divider />
        <List>{secondaryListItems}</List>
      </Drawer>
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>
          {props.children}
        </Container>
      </main>
    </div>
  );
}

interface ReactProps extends WithStyles<typeof styles> {
}



const drawerWidth = 240;
const menuTextColor = "#eaeaea";

const styles = (theme: Theme) => createStyles({
  root: {
    display: 'flex',
    width: '100%',
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
    background: "#181818",
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    cursor: 'pointer'
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9),
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  adminHome: {
    maxWidth: 120,
    textAlign: 'center',
  },
  flexGrower: {
    flexGrow: 1,
    width: '100%',
  },
  notPlatformAdminMessage: {
    marginRight: '1rem',
    textTransform: "uppercase",
    color: '#ee5555',
  },
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
  },
  container: {
    paddingTop: "2rem",
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: 240,
  },
  menuText: {
    color: menuTextColor,
  },
  menuIcon: {
    marginRight: "0.5rem",
    color: menuTextColor,
  },
});


export default withStyles(styles)(Dashboard);