export enum Role {
  ANON = "ANON", // Not logged in
  USER = "USER", // Logged in as somebody
  PLATFORM_ADMIN = "PLATFORM_ADMIN", // A platform owner superuser (ie us)
  SYSTEM = "SYSTEM" // The system / not a human
}

export enum AccessRule {
  PUBLIC = "PUBLIC", // Anyone can access
  LOGGED_IN = "LOGGED_IN", // Can only access if logged in
  OWNER = "OWNER", // Logged in user must be the owner
  PLATFORM_ADMIN = "PLATFORM_ADMIN", // Platform admins only
  SYSTEM = "SYSTEM" // System operation only / not for humans
}

