import * as React from 'react';
import { withStyles, WithStyles } from '@material-ui/core/styles';
import { ReduxStateLogin } from 'reduxStore/login-reducer';

import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';

import { Cart } from "typings";
import styles from './commonStyles';
import SignUp from './SignUp';
import SignIn from './SignIn';
import ResetPassword from './ResetPassword';
import ErrorBounds from "components/ErrorBounds";



class LoginPageModal extends React.Component<ReactProps, any> {

  renderLoginTab = () => {

    const { tabIndex, setTabIndex } = this.props;
    const props = this.props;

    if (tabIndex === 0) {
      return <SignIn
                dispatchLogin={props.dispatchLogin}
                setTabIndex={setTabIndex}
              />
    } else if (tabIndex === 1) {
      return <SignUp
                dispatchCreateUser={props.dispatchCreateUser}
                setTabIndex={setTabIndex}
              />
    } else {
      return <SignIn
                dispatchLogin={props.dispatchLogin}
                setTabIndex={setTabIndex}
              />
    }
  }

  render() {
    const { classes } = this.props;
    const props = this.props;
    return (
      <ErrorBounds>
        {
          !this.props.loggedIn &&
          <>
            <Dialog
              open={props.openModal}
              onClose={props.handleToggleModal}
              // fullScreen={false}
              // fullWidth={true}
              maxWidth="xs"
              classes={{
                paperFullWidth: classes.dialogPaperFull,
                paper: classes.dialogPaper,
              }}
            >
              <div className={classes.outerContainer}>
                <div className={classes.loginButton}>
                  { this.renderLoginTab() }
                </div>
              </div>
            </Dialog>
            <Button
              variant="outlined"
              color="secondary"
              className={classes.loginButton}
              onClick={props.handleToggleModal}
              {...props.buttonProps}
            >
              { props.title ? props.title : "Login" }
            </Button>
          </>
        }
      </ErrorBounds>
    )
  }
}

interface ReactProps extends WithStyles<typeof styles> {
  title?: string;
  tabIndex: number;
  loggedIn: boolean;
  openModal: boolean;
  buttonProps?: any;
  handleToggleModal(): void;
  setTabIndex(tabIndex: number): void;
  dispatchLogin(payload: { email: string, password: string }): void;
  dispatchCreateUser(payload: ReduxStateLogin): void;
  dispatchResetPassword(payload: { email: string }): void;
}

export default withStyles(styles)( LoginPageModal );
