import React from "react";
import { NextPage, NextPageContext } from 'next';
import { oc as option } from "ts-optchain";
// Redux
import { Actions } from "reduxStore/actions";
// Graphql
import { useQuery } from "@apollo/react-hooks";
import { GET_USER } from "queries/user-queries";
import { UserPrivate, Role } from 'typings';

import { useDispatch } from "react-redux";
import { client } from "utils/apollo";
import ApolloClient from "apollo-client";





const GetUser = (props: ReactProps) => {

  const dispatch = useDispatch();

  const { loading, error, data } = useQuery<QueryData>(GET_USER, {
    onCompleted: (data: QueryData) => {},
    onError: (error) => console.log(error.message),
    errorPolicy: "all",
    fetchPolicy: "network-only",
  });

  React.useEffect(() => {
    if (props.user) {
      dispatch(Actions.Login.SET_USER(data.user))
    } else if (option(data).user()) {
      dispatch(Actions.Login.SET_USER(data.user))
    } else {
      client.query({ query: GET_USER })
        .then(({ data }: { data: QueryData }) =>
          dispatch(Actions.Login.SET_USER(data.user))
        )
    }
  }, [loading, option(data)])

  return <div className="get-user"></div>;
};

interface ReactProps {
  user?: UserPrivate;
}
interface QueryData {
  user: UserPrivate;
}

export default GetUser;

