
// Redux
import { createStore, applyMiddleware, compose, combineReducers } from "redux";
import thunk from "redux-thunk";
// State Typings
import { ReduxStateLogin } from './login-reducer';
import { ReduxStateFrenzy } from './frenzy-reducer';
// Reducers
import { reduxReducerLogin } from "./login-reducer";
import { reduxReducerFrenzy } from "./frenzy-reducer";



export const makeStore = (initialState, options) => {
    return createStore(
      combineReducers({
        reduxLogin: reduxReducerLogin,
        reduxFrenzy: reduxReducerFrenzy,
      }),
      initialState,
      applyMiddleware(thunk),
    );
};


export interface GrandReduxState {
  reduxFrenzy: ReduxStateFrenzy;
  reduxLogin: ReduxStateLogin;
}

