import { ActionType } from './actions'
import { reduxFrenzyActions as Actions } from "./frenzy-actions";
import { CreateOperationStatus } from "typings/frenzyBackendTypings";
import { PageCursor, Discount } from "typings/efcBackendTypings";
import { PageState } from "typings";


////// Product state reducer //////////
export interface ReduxStateFrenzy {
  generateProductsStatus: CreateOperationStatus | null;
  currentPlatformDiscountsPageState: PageState<Discount>;
  currentStoreDiscountsPageState: PageState<Discount>;
}

const initialFrenzyState: ReduxStateFrenzy = {
  generateProductsStatus: null,
  currentPlatformDiscountsPageState: {
    isLoading: false,
    page: null
  },
  currentStoreDiscountsPageState: {
    isLoading: false,
    page: null
  }
}



export const reduxReducerFrenzy = (
  state: ReduxStateFrenzy = initialFrenzyState,
  action: ActionType
): ReduxStateFrenzy => {

  switch ( action.type ) {

    case Actions.PRODUCT_GENERATION_STARTED().type: {
      // does nothing?
      return {
        ...state
      }
    }

    // ProductGenerationStatusUpdated
    case Actions.PRODUCT_GENERATION_STATUS_UPDATED().type: {
      let storeDiscounts = state.currentStoreDiscountsPageState;
      if (!action.payload) {
        storeDiscounts = {
          ...storeDiscounts,
          page: null
        };
      }
      return {
        ...state,
        currentStoreDiscountsPageState: storeDiscounts,
        generateProductsStatus: action.payload
      };
    }

    case Actions.PLATFORM_DISCOUNT_CREATED().type: {
      return {
        ...state,
        currentPlatformDiscountsPageState: {
          ...state.currentPlatformDiscountsPageState,
          page: null
        }
      };
    }

    case Actions.PLATFORM_DISCOUNT_EDITED().type: {
      return {
        ...state,
        currentPlatformDiscountsPageState: {
          ...state.currentPlatformDiscountsPageState,
          page: null
        }
      };
    }

    case Actions.GET_PAGE_OF_PLATFORM_DISCOUNTS_STARTED().type: {
      return {
        ...state,
        currentPlatformDiscountsPageState: {
          isLoading: true,
          page: state.currentPlatformDiscountsPageState.page
        }
      };
    }
    case Actions.GET_PAGE_OF_PLATFORM_DISCOUNTS_FINISHED().type: {
      return {
        ...state,
        currentPlatformDiscountsPageState: {
          isLoading: false,
          page: action.payload
        }
      };
    }

    case Actions.GET_PAGE_OF_STORE_DISCOUNTS_STARTED().type: {
      return {
        ...state,
        currentStoreDiscountsPageState: {
          isLoading: true,
          page: state.currentStoreDiscountsPageState.page
        }
      };
    }

    case Actions.GET_PAGE_OF_STORE_DISCOUNTS_FINISHED().type: {
      return {
        ...state,
        currentStoreDiscountsPageState: {
          isLoading: false,
          page: action.payload
        }
      };
    }

    default: {
      return state
    }
  }
}




