
import { ID, User, PaymentProcessor, MakeRefundParams } from "typings";

export const EFC_USER_URL = `${process.env.BACKEND_URL}/user`;
export const EFC_PAYMENT_URL = `${process.env.BACKEND_URL}/payment`;



export const getlogin = async({ email, password }): Promise<string> => {
  // name must match localhost for mock localhost ssl certs on server to work
  let res = await fetch(`${EFC_USER_URL}/login`, {
    method: "POST",
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json',
    },
    credentials: 'include', // develop
    // credentials: 'same-origin', // production
    body: JSON.stringify({
        email: email,
        password: password,
    })
  });

  let data = await res.json();
  let jwt: string = data.jwt
  // localStorage.setItem("efc-auth", auth)
  return jwt
}

export const logout = async(): Promise<LogoutResponse> => {
  // name must match localhost for mock localhost ssl certs on server to work
  let res = await fetch(`${EFC_USER_URL}/logout`, {
    method: "DELETE",
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json',
    },
    credentials: 'include', // develop
    // credentials: 'same-origin', // production
  });
  let data: LogoutResponse = await res.json();
  localStorage.removeItem("efc-login-valid-until");
  window.location.replace('/')
  // back to public product gallery page
  return data
}

interface LogoutResponse {
  status?: string;
}

export const changePassword = async(
  currentPassword: string,
  newPassword: string,
): Promise<User> => {
  return fetch(`${EFC_USER_URL}/auth/profile/changePassword`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    credentials: 'include', // develop
    body: JSON.stringify({
      current_password: currentPassword,
      new_password: newPassword,
    })
  }).then(res => {
    if (res.ok) {
      return res.json()
    } else {
      throw new Error("Password change Error")
    }
  }).catch(err => console.log(err))
}


export const makeRefund = async({
  orderId,
  refundOrderItems,
  chargeId,
  taxes,
  paymentProcessingFee,
  reason,
  reasonDetail,
  paymentIntentId,
  paypalInvoiceNumber,
  paymentProcessor,
}: MakeRefundParams): Promise<any> => {

  let body: MakeRefundParams = {
    orderId: orderId,
    refundOrderItems: refundOrderItems,
    chargeId: chargeId,
    taxes: taxes,
    paymentProcessingFee: paymentProcessingFee,
    reason: reason,
    reasonDetail: reasonDetail,
    paymentIntentId: paymentIntentId,
    paypalInvoiceNumber: paypalInvoiceNumber,
    paymentProcessor: paymentProcessor,
  };

  let res = await fetch(`${EFC_PAYMENT_URL}/refund`, {
    method: "POST",
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json',
    },
    credentials: 'include', // develop
    // credentials: 'same-origin', // production
    body: JSON.stringify(body)
  });

  let data = await res.json();
  console.log("refund response:", data)
  alert(JSON.stringify({ "REFUND": data }))
  return data
}
