import gql from "graphql-tag";
import { UserPrivateFragment } from "./fragments";



export const LOGIN = gql`
mutation (
  $email: String!,
  $password: String!
) {
  logInUsingEmail(
    email: $email,
    password: $password,
  ) {
    jwt
    user {
      ...UserPrivateFragment
    }
  }
}
${UserPrivateFragment}
`;


export const GET_USER = gql`
query {
  user {
    id
    firstName
    lastName
    email
    ... on UserPrivate {
      ...UserPrivateFragment
    }
  }
}
${UserPrivateFragment}
`;

