import * as React from 'react';
import { withStyles, WithStyles } from "@material-ui/core/styles";

import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import PersonAdd from '@material-ui/icons/PersonAdd';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
// Typings
import styles from './commonStyles';
import ErrorBounds from "components/ErrorBounds";
import LockIcon from "@material-ui/icons/Lock";




class SignUp extends React.Component<ReactProps, any> {

  state = {
    email: "",
    password: "",
    firstName: "",
    lastName: "",
    subscribedNewsletters: [],
    emailUpdateCount: 0,
  }

  componentDidUpdate(prevProps, prevState) {
  }

  componentWillUnmount() {
    // if createUser is successful, component unmounts, so reset everything.
    this.setState((state, props) => {
      return {
        email: "",
        password: "",
        firstName: "",
        lastName: "",
        subscribedNewsletters: [],
        emailUpdateCount: 0,
      }
    });
  }

  handleClick = (event) => {
    event.preventDefault();
    this.props.dispatchCreateUser({
      email: this.state.email,
      password: this.state.password,
      firstName: this.state.firstName,
      lastName: this.state.lastName,
      subscribedNewsletters: this.state.subscribedNewsletters,
    });
  }

  toSignup = () => {
    this.props.setTabIndex(0)
  }

  toggleSubscribeToNewsletter = () => {
    if (this.state.subscribedNewsletters.length !== 0 ) {
      this.setState({ subscribedNewsletters: ['promotions'] })
    } else {
      this.setState({ subscribedNewsletters: [] })
    }
  }

  render() {
    let { classes } = this.props;
    return (
    <ErrorBounds className={classes.outerContainer}>
      <div className={classes.paper}>

        <Typography className={classes.title} variant="h5">
          Create an Account
        </Typography>
        <Typography variant="body2">
          Create an account so you can save your downloads or sell your files
        </Typography>

        <form className={classes.form}>
          <FormControl margin="normal" required fullWidth>
            <InputLabel htmlFor="sign-up-email">Email Address</InputLabel>
            <Input autoFocus
              name="sign-up-email"
              autoComplete="email"
              value={this.state.email}
              onChange={(e) => this.setState({ email: e.target.value })}
            />
          </FormControl>
          <FormControl margin="normal" required fullWidth>
            <InputLabel htmlFor="password">
              Password
              <LockIcon className={classes.secureCheckoutIcon}/>
            </InputLabel>
            <Input
              name="sign-up-password"
              autoComplete="current-password"
              type="password"
              onChange={(e) => this.setState({ password: e.target.value })}
            />
          </FormControl>
          <FormControl margin="normal" fullWidth>
            <InputLabel htmlFor="name">Name</InputLabel>
            <Input
              name="first-name"
              value={this.state.firstName}
              onChange={(e) => this.setState({ firstName: e.target.value })}
            />
          </FormControl>
          <FormControl margin="normal" fullWidth>
            <InputLabel htmlFor="last-name">Last Name</InputLabel>
            <Input
              name="last-name"
              value={this.state.lastName}
              onChange={(e) => this.setState({ lastName: e.target.value })}
            />
          </FormControl>
          <FormControlLabel
            classes={{
              label: classes.checkBox
            }}
            control={<Checkbox value="remember" color="primary" />}
            label="Receive discounts, updates, and promotions"
            onClick={this.toggleSubscribeToNewsletter}
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="secondary"
            className={classes.submit}
            onClick={(event) => this.handleClick(event)}
          >
            Create Account
          </Button>

          <Typography variant="caption">
            By signing up, I agree to the Terms of Use and Privacy Policy.
          </Typography>
        </form>

        <div className={classes.preHeader}>
          <Typography variant="body1">
            {"Already have an account? "}
            <a onClick={this.toSignup} className={classes.link}>
              Login
            </a>
          </Typography>
        </div>

      </div>
    </ErrorBounds>
    )
  };
};

interface ReactProps extends WithStyles<typeof styles> {
  setTabIndex(tabIndex: number): void;
  dispatchCreateUser?(payload: any): void;
  email?: string;
}

export default withStyles(styles)(SignUp);
