export type ID = string;
export type Cookie = string;
export type Timestamp = Date;
export type Price = number;
export type Day = number;
export type Email = string;

export interface CreatePlatformDiscountInput {
  promoCode: string | undefined;
  valuePercentageOff: number;
  start: Timestamp | undefined;
  end: Timestamp | undefined;
  isDisabled: boolean;
}

export interface EditPlatformDiscountInput {
  discountId: ID;
  isDisabled: boolean;
}

// COLLECTION/CONNECTION TYPES

export type PageCursor = string;

export interface ConnectionQuery {
  sortAscending: boolean;
  cursor: PageCursor | null;
  pageBackwards: boolean;
  count: number;
}

export interface PageInfo {
  endCursor?: PageCursor;
  isLastPage: boolean;
}

export interface Connection<T> {
  pageInfo: PageInfo;
  edges: Edge<T>[];
  totalCount?: number;
}

export interface Edge<T> {
  cursor: PageCursor;
  node: T;
}

export interface PageBasedConnectionQuery {
  sortAscending: boolean | null;
  pageNumber: number;
  count: number;
}

export interface PageBasedConnectionPageInfo {
  pageNumber: number;
  isLastPage: boolean;
}

export interface PageBasedConnection<T> {
  pageInfo: PageBasedConnectionPageInfo;
  edges: PageBasedEdge<T>[];
}

export interface PageBasedEdge<T> {
  pageNumber: number;
  node: T;
}


// DISCOUNTS

export interface Discount {
  id: ID;
  createdAt: Date;
  modifier: DiscountModifier;
  scope: DiscountScope;

  isDisabled: boolean;

  timeCondition: DiscountTimeCondition | null;
  stockLimitCondition: DiscountStockLimitCondition | null;

  isAutomatic: boolean;
  promoCode: string | null;

  valueFixed: Price | null;
  valueDollarsOff: Price | null;
  valuePercentageOff: number | null;

  productScopeInfo: ProductScopedDiscountInfo | null;
  storeScopeInfo: StoreScopedDiscountInfo | null;
  platformScopeInfo: PlatformScopedDiscountInfo | null;
}

export enum DiscountModifier {
  FIXED_PRICE = "FIXED_PRICE",
  DOLLARS_OFF = "DOLLARS_OFF",
  PERCENTAGE_OFF = "PERCENTAGE_OFF"
}

export enum DiscountScope {
  PRODUCT = "PRODUCT",
  STORE = "STORE",
  PLATFORM = "PLATFORM",
}

export interface DiscountTimeCondition {
  start: Date | null;
  end: Date;
  timeExpiryRule: DiscountUnavailableRule;
}

export interface LimitedSupply {
  quantityTotal: number;
  quantityAvailable: number;
  quantitySold: number;
}

export enum DiscountUnavailableRule {
  DISABLE_DISCOUNT = "DISABLE_DISCOUNT",
  MARK_AS_SOLD_OUT = "MARK_AS_SOLD_OUT"
}

export interface ProductScopedDiscountInfo {
  variantSnapshotId: ID;
}

export interface StoreScopedDiscountInfo {
  storeId: ID;
  productId: ID | null;
  variantId: ID | null;
  minimumSpend: Price | null;
  minimumQuantity: number | null;
}

export interface PlatformScopedDiscountInfo {
  isApplicableToAnyProduct: boolean;
}

export interface ProductSpecialDeal {
  discountedPrice: Price;
  timeCondition: DiscountTimeCondition | null;
  stockLimitCondition: DiscountStockLimitCondition | null;
}

export interface ProductSpecialDeal {
  discountedPrice: Price;
  timeCondition: DiscountTimeCondition | null;
  stockLimitCondition: DiscountStockLimitCondition | null;
}

export interface DiscountTimeCondition {
  start: Date | null;
  end: Date;
  timeExpiryRule: DiscountUnavailableRule;
}

export interface DiscountStockLimitCondition {
  quantityTotal?: number;
  quantityAvailable: number;
  quantitySold?: number;
  supplyExhaustionRule: DiscountUnavailableRule;
}
