import * as React from "react";
import { withStyles, WithStyles, Theme, createStyles } from "@material-ui/core/styles";
import { oc as option } from "ts-optchain";
// Redux
import { connect } from "react-redux"
import { GrandReduxState } from 'reduxStore/grand-reducer';
import { ReduxStateLogin } from 'reduxStore/login-reducer';
import { Actions } from 'reduxStore/actions';
// Graphql
import { LOGIN, GET_USER } from "queries/user-queries";
import { UserPrivate } from "typings";

import LoginPageModal from "./LoginPageModal";

import SnackBarA from "components/Snackbars/SnackbarA";
import { client } from "utils/apollo";
import { logout } from "utils/requests";
import {
  setLoginExpiration,
  runOnLoginExpiration,
  checkThenSetLoggedInStatus
} from "./utils";


/////////////////////////////////////////
//////// Login Modal Component //////////
/////////////////////////////////////////


class Login extends React.Component<ReactProps & ReduxProps, any> {

  state = {
    openModal: false,
    loading: false,
    showError: false,
    dataExists: false,
    error: { message: "" },
    status: "",
    tabIndex: 0,
  }

  componentDidMount() {
    checkThenSetLoggedInStatus(() => {
      this.props.updateLoginState({ loggedIn: true })
    });
    runOnLoginExpiration(() => {
      this.props.updateLoginState({ loggedIn: false })
      logout()
    })
  }

  handleToggleModal = () => {
    if (this.state.openModal) {
      this.setState({ tabIndex: 0 })
    }
    this.setState({ openModal: !this.state.openModal })
  }

  setTabIndex = (value: number) => this.setState({ tabIndex: value });

  userNotFound = (errMessage: string) => {
    return /[Nn]ot\s*[Ff]ound/.exec(errMessage.toLowerCase())
  }

  isLoginInputOk = ({ email, password }) => {
    if (!email) {
      this.setState({ status: "Email is missing!" })
      return false
    } else if (!password) {
      this.setState({ status: "Password is missing!" })
      return false
    } else {
    return true
    }
  }

  updateLoginState = (user, loading, errors) => {
    // Update login UI state
    this.props.updateLoginState({
      user: user,
      loading,
      loggedIn: true,
      error: errors,
    });
    // Set loginExpiration token and countdown timer
    setLoginExpiration(24);
    // Popup Snackbar
    this.setState({ dataExists: true, loading: false });
    // Close modal after a delay
    setTimeout(() => {
      this.setState({ openModal: false, tabIndex: 0 })
    }, 900);
  }

  handleGraphQLResponse = ({ data, loading, errors }: Aprops) => {
    if (errors !== undefined) {
      this.handleGqlError(errors)
    }

    if (data.logInUsingEmail) {
      // optional chaining
      let user = option(data.logInUsingEmail).user()
      // Update apollo cache
      client.cache.writeQuery({
        query: GET_USER,
        data: {
          user: {
            ...user,
          }
        }
      });
      this.updateLoginState(user, loading, errors)
    }
  }

  handleGqlError = (errors) => {
    this.setState({
      showError: true,
      error: errors[0], // Apollo error/errors API mismatch
      loading: false,
    });
    // make sure "errorPolicy == all" for GraphQL requests
    if (this.userNotFound(errors[0].message)) {
      setTimeout(() => this.setState({ tabIndex: 1 }), 300);
      // switch to register new user
    }
  }

  handleLogout = async() => {
    this.setState({ loading: true });
    let { status } = await logout();
    this.props.updateLoginState({
      loggedIn: false,
      user: undefined,
      email: "",
      loading: false,
      status: status,
    })
    this.setState({ loading: false, status: status });
  }

  dispatchLogin = async({ email, password }: { email: string; password: string}) => {

    if (!this.isLoginInputOk({ email, password })) {
      return null
    } else {
      this.setState({ loading: true });
    }

    let { data, loading, errors }: Aprops = await client.query({
      query: LOGIN,
      variables: {
        email: email.trim(),
        password: password,
      },
      fetchPolicy: "no-cache", // always do a network request, no caches
      errorPolicy: "all", // propagate errors from backend to Snackbar
    });

    this.handleGraphQLResponse({ data, loading, errors });
  }


  render() {
    let {
      classes,
      title = "Login",
    } = this.props;
    return (
      <>
        <LoginPageModal
          title={title}
          loggedIn={this.props.reduxLogin.loggedIn}
          tabIndex={this.state.tabIndex}
          setTabIndex={this.setTabIndex}
          dispatchLogin={this.dispatchLogin}
          dispatchCreateUser={() => {}}
          openModal={this.state.openModal}
          dispatchResetPassword={() => {}}
          handleToggleModal={this.handleToggleModal}
          buttonProps={this.props.buttonProps}
        />

        <div className={classes.modal}>
          <SnackBarA
            open={this.state.loading}
            closeSnackbar={() => this.setState({ loading: false })}
            message={"Checking user login..."}
            variant={"info"}
          />
          <SnackBarA
            open={this.state.showError}
            closeSnackbar={() => {
              this.setState({ showError: false });
              // wait for Snackbar to move off-screen, before resetting error
              setTimeout(() => this.setState({ error: undefined }), 300);
            }}
            message={this.state.error ? this.state.error.message : ""}
            variant={"error"}
          />
          <SnackBarA
            open={this.props.reduxLogin.loggedIn && this.state.dataExists}
            closeSnackbar={() => this.setState({ dataExists: false })}
            message={"Successful login!"}
            variant={"success"}
          />
          <SnackBarA
            open={this.state.status.length > 0}
            closeSnackbar={() => this.setState({ status: "" })}
            message={this.state.status}
            variant={"info"}
          />
        </div>
      </>
    );
  };
};


interface ReduxProps {
  data?: { user: UserPrivate };
  reduxLogin: ReduxStateLogin;
  updateLoginState(payload: ReduxStateLogin): void;
  title?: string;
}
interface ReactProps extends WithStyles<typeof styles> {
  buttonVariant?: string;
  buttonProps?: any;
}
interface Aprops {
  data?: {
    logInUsingEmail?: { user: UserPrivate };
  };
  loading?: boolean;
  errors?: any;
}

const mapStateToProps = ( state: GrandReduxState ) => {
  return {
    reduxLogin: state.reduxLogin,
  }
}

const mapDispatchToProps = ( dispatch ) => {
  return {
    updateLoginState: (payload: ReduxStateLogin) => dispatch(
      Actions.Login.UPDATE_LOGIN_STATE(payload)
    ),
  }
}



const styles = (theme: Theme) => createStyles({
  dialogPaperFull: {
    width: "calc(100% - 0.5rem)",
  },
  dialogPaper: {
    margin: '1rem',
  },
  modal: {
    position: "fixed",
    zIndex: 100,
    bottom: "20px",
    right: "20px",
  },
  outerContainer: {
    maxWidth: '400px',
    padding: '2%',
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center',
  },
  preHeader: {
    marginTop: "1rem",
  },
  link: {
    color: "#2484FF",
    cursor: 'pointer',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  avatarSignin: {
    margin: theme.spacing(1),
    color: '#fff',
    backgroundColor: theme.palette.primary.main,
  },
  avatarSignup: {
    margin: theme.spacing(1),
    color: '#fff',
    backgroundColor: theme.palette.primary.main,
  },
  checkBox: {
    fontSize: "0.8rem",
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    marginTop: theme.spacing(1),
  },
  secureCheckout: {
    color: "#bbbbbb",
  },
  secureCheckoutIcon: {
    color: "#bbbbbb",
    height: '0.8rem',
    width: '0.8rem',
    marginRight: "0.1rem",
  },
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)( Login ))
