import { ApolloClient } from 'apollo-client';
import { InMemoryCache } from 'apollo-cache-inmemory';
import { HttpLink } from 'apollo-link-http';
import { onError } from 'apollo-link-error';
import { ApolloLink } from 'apollo-link';
// Switches between unfetch & node-fetch for client & server.
import fetch from 'isomorphic-unfetch'
import withApollo from 'next-with-apollo';
import https from "https";
const dev = process.env.NODE_ENV !== 'production'



console.log("REACT_APP_GATEWAY_GRAPHQL_URL: ", process.env.REACT_APP_GATEWAY_GRAPHQL_URL)
console.log("Apollo url: ", "https://0.0.0.0:8080/gateway/graphql")

// Fragments Schema
import { IntrospectionFragmentMatcher } from 'apollo-cache-inmemory';
import introspectionQueryResultData from 'queries/fragmentTypes.json';
const fragmentMatcher = new IntrospectionFragmentMatcher({
  introspectionQueryResultData
});


// SSR Apollo. Function to return a new instance of ApolloClient
// with every request
export default withApollo(
  ({ ctx, headers, initialState }) => {
    return new ApolloClient({
      link: ApolloLink.from([
        onError(({ graphQLErrors, networkError }) => {
          if (graphQLErrors)
            graphQLErrors.forEach(({ message, locations, path }) =>
              console.log(
                `[GraphQL error]: Message:
                ${message},
                Location: ${locations},
                Path: ${path}`,
              ),
            );
          if (networkError) console.log(`[Network error]: ${networkError}`);
        }),
        new HttpLink({
          uri: process.env.REACT_APP_GATEWAY_GRAPHQL_URL,
          fetch: fetch,
          fetchOptions:
            dev ? { agent: new https.Agent({ rejectUnauthorized: false }) } : {},
          credentials: 'include',
        })
      ]),
      // hydrates apollo cache with initialState created in server
      cache: new InMemoryCache({
        fragmentMatcher, // fragments
      }).restore(initialState || {}),
      ssrMode: true,
    })
  },
  {
    getDataFromTree: 'ssr'
    // Should the apollo store be hydrated before the first render?,
    // allowed values are always, never or ssr (don't hydrate on client side navigation)
    // Don't use always:
    // https://github.com/mui-org/material-ui/issues/15798
  }
)


// only for functions in /reduxStore/frenzy-thunks.ts
export const client = new ApolloClient({
  link: ApolloLink.from([
    onError(({ graphQLErrors, networkError }) => {
      if (graphQLErrors)
        graphQLErrors.forEach(({ message, locations, path }) =>
          console.log(
            `[GraphQL error]: Message:
            ${message},
            Location: ${locations},
            Path: ${path}`,
          ),
        );
      if (networkError) console.log(`[Network error]: ${networkError}`);
    }),
    new HttpLink({
      uri: process.env.REACT_APP_GATEWAY_GRAPHQL_URL,
      fetch: fetch,
      fetchOptions: dev
        ? {
          agent: new https.Agent({ rejectUnauthorized: false }),
        } : {},
      credentials: 'include',
    })
  ]),
  // hydrates apollo cache with initialState created in server
  cache: new InMemoryCache({
    fragmentMatcher, // fragments
  }),
  ssrMode: true,
});